<template>
  <section class="step-7">
    <div class="add-place-des">
      <h4>Date & Time</h4>
    </div>
    <div class="step7-body">
      <div class="row">
        <div class="col-6">
          <label class="check-container">
            <span class="check-txt">
              Available all month days
            </span>
            <input type="checkbox" v-model="availableAllMonth" />
            <span class="checkmark"></span>
          </label>

          <ValidationObserver v-slot="{ invalid }">
            <form @submit.prevent="" class="form">
              <ValidationProvider
                name="maxStayPeriod"
                rules="required|numeric"
                v-slot="{ errors }"
              >
                <div class="form-input-wrapper">
                  <label for="">Maximum stay period (days)</label>
                  <div class="form-input-element">
                    <input v-model="maxStayPeriod" type="number" />
                  </div>

                  <span class="form-error">{{ errors[0] }}</span>
                </div>
              </ValidationProvider>

              <ValidationProvider
                name="minStayPeriod"
                rules="required|numeric"
                v-slot="{ errors }"
              >
                <div class="form-input-wrapper">
                  <label for="">Minimum stay period (days)</label>
                  <div class="form-input-element">
                    <input v-model="minStayPeriod" type="number" />
                  </div>

                  <span class="form-error">{{ errors[0] }}</span>
                </div>
              </ValidationProvider>

              <div class="check-time">
                <span class="check">Check In Time:</span>
                <vc-date-picker
                  v-model="checkInTime"
                  mode="time"
                  :minute-increment="5"
                />
              </div>

              <div class="check-time">
                <span class="check">Check Out Time:</span>
                <vc-date-picker
                  v-model="checkOutTime"
                  mode="time"
                  :minute-increment="5"
                />
              </div>

              <Navigation :valid="!invalid" />
            </form>
          </ValidationObserver>
        </div>
        <div class="col-4">
          <transition name="fade">
            <div class="month-days-calendar" v-if="!availableAllMonth">
              <span>Choose available month days</span>
              <vc-calendar :attributes="attributes" @dayclick="onDayClick" />
            </div>
          </transition>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { numeric, required } from "vee-validate/dist/rules";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

extend("numeric", {
  ...numeric,
  message: "This field only accept numbers",
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {
      days: [],
      date: null,
      timezone: "",
    };
  },
  computed: {
    availableAllMonth: {
      get() {
        return this.$store.state.steps.step7.availableAllMonth;
      },
      set(value) {
        this.$store.commit("setAvailableAllMonth", {
          availableAllMonth: value,
        });
      },
    },
    daysAvailable: {
      get() {
        return this.$store.state.steps.step7.daysAvailable;
      },
      set(value) {
        this.$store.commit("setDaysAvailabele", { daysAvailable: value });
      },
    },
    maxStayPeriod: {
      get() {
        return this.$store.state.steps.step7.maxStayPeriod;
      },
      set(value) {
        this.$store.commit("setMaxStayPeriod", { maxStayPeriod: value });
      },
    },
    minStayPeriod: {
      get() {
        return this.$store.state.steps.step7.minStayPeriod;
      },
      set(value) {
        this.$store.commit("setMinStayPeriod", { minStayPeriod: value });
      },
    },
    checkInTime: {
      get() {
        return this.$store.state.steps.step7.checkInTime;
      },
      set(value) {
        this.$store.commit("setCheckInTime", { checkInTime: value });
      },
    },
    checkOutTime: {
      get() {
        return this.$store.state.steps.step7.checkOutTime;
      },
      set(value) {
        this.$store.commit("setCheckOutTime", { checkOutTime: value });
      },
    },
    pricePerDay: {
      get() {
        return this.$store.state.steps.step7.pricePerDay;
      },
      set(value) {
        this.$store.commit("setPricePerDay", { pricePerDay: value });
      },
    },
    hasDiscount: {
      get() {
        return this.$store.state.steps.step7.longPeriodDiscount.hasDiscount;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscount", { hasDiscount: value });
      },
    },
    longPeriodDiscountPeriod: {
      get() {
        return this.$store.state.steps.step7.longPeriodDiscount
          .longPeriodDiscountPeriod;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscountPeriod", {
          longPeriodDiscountPeriod: value,
        });
      },
    },
    longPeriodDiscountDiscount: {
      get() {
        return this.$store.state.steps.step7.longPeriodDiscount
          .longPeriodDiscountDiscount;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscountDiscount", {
          longPeriodDiscountDiscount: value,
        });
      },
    },
    dates() {
      return this.days.map((day) => day.date);
    },
    attributes() {
      return this.dates.map((date) => ({
        highlight: true,
        dates: date,
      }));
    },
  },
  methods: {
    onDayClick(day) {
      const idx = this.days.findIndex((d) => d.id === day.id);
      if (idx >= 0) {
        this.days.splice(idx, 1);
      } else {
        this.days.push({
          id: day.id,
          date: day.date,
        });
      }
    },
  },
};
</script>

<style scoped lang="scss">
/* Customize the label (the container) */
.check-container {
  display: block;
  position: relative;
  padding-left: 1.7rem;
  cursor: pointer;
  font-size: 1.2rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0.5rem;
}

/* Hide the browser's default checkbox */
.check-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 5px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid $text;
  border-radius: 5px;
}

/* On mouse-over, add a grey background color */
.check-container:hover input ~ .checkmark {
  border-color: $textDark;
}

/* When the checkbox is checked, add a blue background */
.check-container input:checked ~ .checkmark {
  background-color: $primary;
  border: none;
  color: $white;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.month-days-calendar {
  margin-left: 1rem;
  span {
    display: block;
    font-size: 1rem;
    color: $text;
  }
}
.check {
  font-size: 1.2rem;
  margin-right: 1rem;
}
.check-time {
  margin-top: 1rem;
  span {
    display: block;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
